<template>
    <view style="height: 100%">
        <template v-if="compName === 'movieSection'">
            <navigator class="movie-section" :url="'/pages/subPages/movie-detail/movie-detail?movieId=' + movie.id">
                <image class="movie-img" :src="movie.img"></image>
                <view class="movie-info">
                    <view class="movie-name line-ellipsis">
                        {{ movie.nm }}
                        <text :class="'version ' + movie.version"></text>
                    </view>
                    <view class="movie-score line-ellipsis">
                        <view v-if="movie.globalReleased">
                            <text v-if="movie.sc">
                                观众评
                                <text class="grade">{{ movie.sc }}</text>
                            </text>
                            <text v-else>暂无评分</text>
                        </view>
                        <view v-else>
                            <text class="grade">{{ movie.wish }}</text>
                            人想看
                        </view>
                    </view>
                    <view class="movie-star line-ellipsis">主演：{{ movie.star }}</view>
                    <view v-if="rt">{{ movie.rt }}上映</view>
                    <view v-else class="movie-showInfo line-ellipsis">{{ movie.showInfo }}</view>
                </view>
                <view class="buy-tickets">
                    <view v-if="movie.showst === 3" class="btn">购票</view>
                    <view v-else-if="movie.showst === 1" class="btn want-see">想看</view>
                    <view v-else-if="movie.showst === 4" class="btn pre-sale">预售</view>
                </view>
            </navigator>
        </template>
    </view>
</template>

<script>
export default {
    props: ['data', 'compName'],
    computed: {
        movie() {
            return this.data.movie;
        },
        rt() {
            return this.data.rt;
        }
    }
};
</script>

<style>
.movie-section {
    position: relative;
    display: flex;
    align-items: center;
    height: 228rpx;
    padding: 0 30rpx;
}
.movie-section::after {
    position: absolute;
    content: '';
    height: 0;
    width: calc(100% - 208rpx);
    right: 30rpx;
    bottom: 0;
    border-bottom: 1px solid #f0f0f0;
}
.movie-info {
    margin: 0 20rpx;
    font-size: 26rpx;
    color: #666;
    width: 60%;
}
.movie-info > view {
    margin-bottom: 6rpx;
}
.movie-img {
    width: 128rpx;
    height: 180rpx;
}
.movie-name {
    font-size: 34rpx;
    color: #333;
    padding-right: 10rpx;
}
.version {
    display: inline-block;
    height: 28rpx;
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 10rpx;
    margin-top: 10rpx;
}
.v3d.imax {
    width: 86rpx;
    background-image: url(data:image/png;base64,);
}
.v3d {
    width: 34rpx;
    background-image: url(data:image/png;base64,);
}
.v2d.imax {
    width: 86rpx;
    background-image: url(data:image/png;base64,);
}
.grade {
    font-weight: 700;
    color: #faaf00;
    font-size: 30rpx;
}
</style>
